<template>
  <div class="page-illegal-info">
    <!-- <van-nav-bar title="违法处罚" left-arrow @click-left="onClickNavBarLeft" :fixed="true" /> -->
    <div class="main-content">
      <van-cell-group>
        <van-field required v-model="illegalTime" readonly label="违法时间"></van-field>
        <van-field
          required
          :value="illegalAddress.placeName"
          label="违法地点"
          readonly
          placeholder="请选择违法地点"
          @click="illegalAddressPopShow = true"
        />
        <van-field
          required
          :value="illegalCrossing.placeName"
          label="路口路段"
          readonly
          placeholder="请选择路口路段"
          @click="onShowIllegalCrossing"
        />
        <van-field
          required
          v-model="illegalAddressAndCrossing"
          readonly
          label="处罚地点"
          placeholder="根据违法地点和路口地段生成"
        ></van-field>
        <van-field
          required
          :value="illegalCode.code"
          label="违法代码"
          readonly
          placeholder="请选择违法代码"
          @click="illegalCodesPopShow = true"
        />
        <van-field
          required
          v-model="illegalBehavior"
          type="textarea"
          autosize
          readonly
          label="违法行为"
          placeholder="根据所选违法代码生成"
        ></van-field>
        <van-field
          required
          v-model="punishMoney"
          readonly
          label="罚款金额"
          placeholder="根据所选违法代码生成"
        ></van-field>
        <van-field required clearable label="处罚方式">
          <van-dropdown-menu slot="input" :overlay="false">
            <van-dropdown-item v-model="punishType" :options="punishTypeOptions" />
          </van-dropdown-menu>
        </van-field>
        <van-field
          required
          :value="approvalMen.name"
          label="审核警员"
          readonly
          placeholder="请选择审核警员"
          @click="approvalMensPopShow = true"
        />
      </van-cell-group>
    </div>
    <div class="btn-wrap">
      <div class="btn pre-btn" @click="gotoPrePage">上一步（违法信息）</div>
      <div class="btn print-btn" @click="submit">提交审核</div>
    </div>
    <PopListWithSearch
      :show="illegalAddressPopShow"
      title="请选择违法地点"
      :list="illegalAddressesStr"
      @onItemSelected="onIllegalAddressSelect"
      @onClose="illegalAddressPopShow = false"
    />
    <PopListWithSearch
      :show="illegalCrossingPopShow"
      title="请选择路口路段"
      :list="illegalCrossingesStr"
      @onItemSelected="onIllegalCrossingSelect"
      @onClose="illegalCrossingPopShow = false"
    />
    <PopListWithSearch
      :show="illegalCodesPopShow"
      title="请选择违法代码"
      :list="illegalCodesStr"
      @onItemSelected="onIllegalCodeSelect"
      @onClose="illegalCodesPopShow = false"
    />
    <PopListWithSearch
      :show="approvalMensPopShow"
      title="请选择审核警员"
      :list="approvalMensStr"
      @onItemSelected="onApprovalMenSelect"
      @onClose="approvalMensPopShow = false"
    />
  </div>
</template>

<script>
import Vue from 'vue'
import { mapState, mapActions } from 'vuex'
import moment from 'moment'
import _ from 'underscore'
import qs from 'querystring'
import EXIF from 'exif-js'
import { Toast, CellGroup, Field, Button, DropdownMenu, DropdownItem } from 'vant'
import request from '@/utils/request'
import { valiIdCard, valiPhone, isEmpty, valiCarNumber } from '@/utils/validate'
import PopListWithSearch from '@/components/PopListWithSearch'

Vue.use(Toast)
  .use(CellGroup)
  .use(Field)
  .use(Button)
  .use(DropdownMenu)
  .use(DropdownItem)

function getPhotoOrientation(imgFile) {
  return new Promise((resolve, reject) => {
    EXIF.getData(imgFile, function() {
      const orientation = EXIF.getTag(imgFile, 'Orientation') // 1=>0° 3=>180° 6=>顺时针90° 8=>逆时针90°
      resolve(orientation)
    })
  })
}
async function compressImageFile({ file, maxWidth = 1200, maxHeight = 1200, compressLevel = 0.6 }) {
  const orientation = await getPhotoOrientation(file)
  return new Promise((resolve, reject) => {
    let fileObj = file
    let reader = new FileReader()
    reader.readAsDataURL(fileObj) // 转base64
    reader.onload = function(e) {
      let image = new Image() // 新建一个img标签（还没嵌入DOM节点)
      image.src = e.target.result
      image.onload = function() {
        let canvas = document.createElement('canvas') // 新建canvas
        let context = canvas.getContext('2d')

        let w = image.width
        let h = image.height
        let resizeW = w
        let resizeH = h

        // 计算压缩比例
        if (w > maxWidth || h > maxHeight) {
          let multiple = Math.max(w / maxWidth, h / maxHeight)
          resizeW = w / multiple
          resizeH = h / multiple
        } else {
          resizeW = w
          resizeH = h
        }

        // 针对照片被旋转的情况
        if (orientation === 6) {
          canvas.width = resizeH
          canvas.height = resizeW
          context.rotate((90 * Math.PI) / 180)
          context.drawImage(image, 0, -resizeH, resizeW, resizeH)
        } else {
          canvas.width = resizeW
          canvas.height = resizeH
          context.drawImage(image, 0, 0, resizeW, resizeH)
        }

        let data = canvas.toDataURL('image/jpeg', compressLevel) // 输出压缩后的base64
        let arr = data.split(',')
        let mime = arr[0].match(/:(.*?);/)[1] // 转成blob
        let bstr = atob(arr[1])
        let n = bstr.length
        let u8arr = new Uint8Array(n)
        while (n--) {
          u8arr[n] = bstr.charCodeAt(n)
        }
        let files = new window.File([new Blob([u8arr], { type: mime })], 'test.jpeg', { type: 'image/jpeg' }) // 转成file
        resolve(files)
      }
    }
  })
}

export default {
  name: 'inner',
  components: {
    PopListWithSearch
  },
  data() {
    return {
      illegalTime: moment().format('YYYY-MM-DD HH:mm:ss'),
      punishType: '罚款',
      punishTypeOptions: [
        { text: '罚款', value: '罚款' },
        { text: '教育', value: '教育' }
      ],
      illegalAddressPopShow: false,
      illegalAddress: {},
      illegalAddressesStr: [],
      illegalAddresses: [],

      illegalCrossingPopShow: false,
      illegalCrossing: {},
      illegalCrossinges: [],
      illegalCrossingesStr: [],

      illegalCodesPopShow: false,
      illegalCode: {},
      illegalCodes: [],
      illegalCodesStr: [],

      approvalMensPopShow: false,
      approvalMen: {},
      approvalMens: [],
      approvalMensStr: []
    }
  },
  computed: {
    ...mapState('punishment', ['personInfo', 'illegalBaseInfo']),
    isPersonalType() {
      return this.personnelType === '行人'
    },
    illegalAddressAndCrossing() {
      let str = ''
      if (this.illegalAddress.placeName) {
        str = this.illegalAddress.placeName
      }
      if (this.illegalCrossing.placeName) {
        str = `${str}、${this.illegalCrossing.placeName}`
      }
      return str
    },
    illegalBehavior() {
      if (this.illegalCode && this.illegalCode.violationAction) {
        return this.illegalCode.violationAction
      }
      return ''
    },
    punishMoney() {
      if (this.illegalCode && this.illegalCode.penaltyAmount) {
        return this.illegalCode.penaltyAmount
      }
      return ''
    }
  },

  created() {
    this.getIllegalAddresses()
    this.getillegalCodes()
    this.getApprovalMens()
  },
  watch: {
    illegalAddress() {
      this.illegalCrossing = {}
      this.illegalCrossinges = []
      this.illegalCrossingesStr = []
      this.getillegalCrossinges()
    }
  },
  methods: {
    ...mapActions('punishment', ['PUT_PUNISH_INFO']),
    gotoPrePage() {
      this.$router.go(-1)
    },
    async getIllegalAddresses() {
      const response = await request('/api/roadPlace/getRoadPlace', {
        method: 'post'
      })
      if (response.resultCode === 200) {
        this.illegalAddresses = response.data
        this.illegalAddressesStr = response.data.map(item => item.placeName)
      }
    },
    async getillegalCrossinges() {
      const params = {
        parentCode: this.illegalAddress.placeCode
      }
      const response = await request(`/api/roadPlace/getRoadPlace?${qs.stringify(params)}`, {
        method: 'post'
      })
      if (response.resultCode === 200) {
        this.illegalCrossinges = response.data
        this.illegalCrossingesStr = response.data.map(item => item.placeName)
      }
    },
    async getillegalCodes() {
      const params = {
        code: '',
        pageNo: 1,
        pageSize: 500,
        violationAction: ''
      }
      const response = await request(`/api/tViolationCode/searchPage?${qs.stringify(params)}`, {
        method: 'post'
      })
      if (response.resultCode === 200) {
        this.illegalCodes = response.data.records
        this.illegalCodesStr = this.illegalCodes.map(item => item.code + ' — ' + item.violationAction)
        // console.log(this.illegalCodesStr)
      }
    },
    async getApprovalMens() {
      // empType：1交警，2辅警，3其他
      const params = {
        pageNo: 1,
        pageSize: 500,
        departmentCode: '',
        empType: 1,
        searchStr: ''
      }
      const response = await request(`/api/tEmployee/searchPageByEmpType?${qs.stringify(params)}`, {
        method: 'post'
      })
      if (response.resultCode === 200) {
        this.approvalMens = response.data.records
        this.approvalMensStr = this.approvalMens.map(item => item.name + ' — ' + item.department)
        // console.log(this.illegalCodesStr)
      }
    },
    onShowIllegalCrossing() {
      if (this.illegalAddress.placeName) {
        this.illegalCrossingPopShow = true
      } else {
        Toast('请先选择违法地点')
      }
    },
    onIllegalCrossingSelect(value) {
      this.illegalCrossing = _.find(this.illegalCrossinges, item => item.placeName === value)
    },
    onIllegalAddressSelect(value) {
      this.illegalAddress = _.find(this.illegalAddresses, item => item.placeName === value)
    },
    onIllegalCodeSelect(value) {
      this.illegalCode = _.find(this.illegalCodes, item => item.code + ' — ' + item.violationAction === value)
    },
    onApprovalMenSelect(value) {
      this.approvalMen = _.find(this.approvalMens, item => item.name + ' — ' + item.department === value)
    },
    valid() {
      let passed = true
      const { illegalAddress, illegalCrossing, illegalCode, approvalMen } = this
      let paris = [
        { value: illegalAddress, txt: '请选择违法地址' },
        { value: illegalCrossing, txt: '请选择违法路口' },
        { value: illegalCode, txt: '请选择违法代码' },
        { value: approvalMen, txt: '请选择审核警员' }
      ]
      for (let i = 0; i < paris.length; i++) {
        const item = paris[i]
        if (isEmpty(item.value)) {
          Toast.fail(item.txt)
          passed = false
          break
        }
      }
      return passed
    },
    async submit() {
      if (!this.valid()) {
        return
      }
      Toast.loading({
        message: '提交数据中...',
        duration: 0,
        forbidClick: true
      })
      const { personInfo, illegalBaseInfo } = this
      const imageIds = []
      let imageUploadResponses = []
      if (illegalBaseInfo.images && illegalBaseInfo.images.length) {
        const imageFiles = await Promise.all(
          illegalBaseInfo.images.map(item => {
            return compressImageFile({
              file: item.file,
              maxWidth: 1000,
              compressLevel: 0.6
            })
          })
        )

        const uploadRequests = imageFiles.map(item => {
          const formData = new FormData()
          formData.append('file', item)
          return (async function() {
            const responese = await request(`/api/tFileInfo/fileUpload`, {
              method: 'post',
              data: formData
            })
            return responese
          })()
        })
        imageUploadResponses = await Promise.all(uploadRequests)
      }
      const punishImageIds = imageUploadResponses.map(item => item.data.id).join(',')
      const params = {
        //
        documentNumber: personInfo.idcard,
        documentType: personInfo.certificateType,
        offenderName: personInfo.name,
        //
        phone: illegalBaseInfo.phone,
        carNumber: illegalBaseInfo.carNum,
        carType: illegalBaseInfo.transport == null ? '' : illegalBaseInfo.transport.vehicleName,
        violationType: illegalBaseInfo.personnelType,
        //
        violationTime: this.illegalTime,
        penaltyAmount: this.punishMoney,
        punishPlace: this.illegalAddress.placeName,
        punishRoad: this.illegalCrossing.placeName,
        punishType: this.punishType,
        violationAction: this.illegalBehavior,
        violationCode: this.illegalCode.code,
        violationClause: this.illegalCode.violationClause,
        punishBasis: this.illegalCode.punishBasis,
        approveManId: this.approvalMen.id,
        approveMan: this.approvalMen.name
      }
      if (punishImageIds) {
        params.punishImageIds = punishImageIds
      }
      const response = await request(`/api/tPunishment/fuJingAdd?${qs.stringify(params)}`, {
        method: 'post'
      })
      if (response.resultCode === 200) {
        Toast.success(response.message)
        setTimeout(() => {
          this.$router.push({
            name: 'punish'
          })
        }, 500)
      } else {
        Toast.fail(response.message)
      }
      Toast.clear()
    }
  }
}
</script>

