<template>
  <van-popup class="component-pop_list_witd_search" :value="show" @click-overlay="onClose">
    <div class="title">{{ title }}</div>
    <van-field v-model="searchStr" placeholder="可输入关键字筛选"></van-field>
    <ul class="list-wrap">
      <li
        class="van-hairline--bottom"
        v-for="item in filteredList"
        v-bind:key="item"
        @click="() => onItemClick(item)"
      >
        {{ item }}
      </li>
    </ul>
  </van-popup>
</template>

<script>
import Vue from 'vue'
import { mapState, mapActions } from 'vuex'
import { Popup, Field } from 'vant'
import request from '@/utils/request'

Vue.use(Popup).use(Field)

export default {
  name: 'popListWithSearch',
  components: {},
  props: ['show', 'title', 'list'],
  data() {
    return {
      searchStr: ''
    }
  },
  computed: {
    filteredList() {
      let list = []
      if (this.searchStr.length > 0) {
        this.list.forEach(item => {
          if (item.indexOf(this.searchStr) > -1) {
            list.push(item)
          }
        })
      } else {
        list = [...this.list]
      }
      return list
    }
  },
  methods: {
    onClose() {
      this.$emit('onClose')
    },
    onItemClick(value) {
      this.$emit('onClose')
      this.$emit('onItemSelected', value)
    }
  }
}
</script>

